import React from "react";
import {Dialog, DialogContent, DialogTitle} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";

interface ICheckEmailVerificationDialog {
    onClose?: () => void,
}

const CheckEmailVerificationDialog: React.FC<ICheckEmailVerificationDialog> = ({onClose}) => {
    const [open, setOpen] = React.useState<boolean>(true);

    const onCloseHandler = () => {
        setOpen(false);
        if (onClose) onClose();
    };

    return (
        <Dialog onClose={onCloseHandler} aria-labelledby="simple-dialog-title" open={open}>
            <DialogTitle id="simple-dialog-title">Information</DialogTitle>
            <DialogContent>
                <Grid>
                    <Typography>
                        Please check the email verification in your <strong>Inbox</strong> or <strong>Spam</strong> folder.
                    </Typography>
                </Grid>
            </DialogContent>
        </Dialog>
    );
};

export default CheckEmailVerificationDialog;