import React, {FormEventHandler, useContext, useEffect, useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import {Icon} from '@material-ui/core';
import {Link, useHistory} from 'react-router-dom';
import * as yup from 'yup';
import {toast} from "react-toastify";
import {HOME, SIGN_IN} from "../constants/routes";
import LoadingBackground from "../components/commons/LoadingBackdrop";
import Footer from "../components/Footer";
import InputPassword from "../components/forms/InputPassword";
import {FirebaseContext} from "../contexts";
import CheckEmailVerificationDialog from "../components/dialogs/CheckEmailVerificationDialog";

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        boxShadow: '0 4px 8px 0 rgba(0,0,0,0.2)',
        transition: '0.3s',
        backgroundColor: 'white',
        padding: '50px',
        borderRadius: '10px',
    },
    image: {
        backgroundImage: 'url(./apply/assets/images/um-icamp-2020-background.png)',
        backgroundRepeat: 'no-repeat',
        backgroundColor:
            theme.palette.type === 'dark' ? theme.palette.grey[900] : theme.palette.grey[50],
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        display: 'flex',
        height: '100vh',

    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

const SignUp: React.FC = () => {
    const classes = useStyles();
    const [uid] = useState<String>(localStorage.getItem('uid') as String);
    const firebaseApp = useContext(FirebaseContext);
    const history = useHistory();
    const [email, setEmail] = useState<string>('');
    const [password, setPassword] = useState<string>('');
    const [openDialog, setOpenDialog] = useState<boolean>(false);
    const [loading, setLoading] = useState<boolean>(false);
    const validationSchema = yup.object({
        email: yup
            .string()
            .email(),
        password: yup
            .string()
            .min(6),
    });

    const invalidForm: boolean = !validationSchema.fields.email.isValidSync(email) || !validationSchema.fields.password.isValidSync(password);


    useEffect(() => {
        if (uid) history.push(HOME);

    }, [uid, history]);

    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();
        if (!invalidForm) {
            setLoading(true);

            const res = await firebaseApp?.doCreateUserWithEmailAndPassword(email, password);
            if (res?.hasError()) {
                toast.error(res?.error?.message);
                setLoading(false);
            } else {
                toast.success('Success create account. Please check your email.');
                setOpenDialog(true);
            }

        }
    };

    return (
        <div className={classes.image}>
            <Container component="main" maxWidth="xs">
                <LoadingBackground open={loading}/>
                <CssBaseline/>
                <div className={classes.paper}>
                    <Avatar className={classes.avatar}>
                        <Icon>lock_outlined_icon</Icon>
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign up to UM iCamp 2020
                    </Typography>
                    <form className={classes.form} onSubmit={onSubmitHandler}>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <TextField
                                    fullWidth
                                    required
                                    variant="outlined"
                                    id="email"
                                    name="email"
                                    label="Email Address"
                                    type="email"
                                    autoComplete="email"
                                    onChange={(e) => setEmail(e.currentTarget.value)}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <InputPassword onChange={(e) => setPassword(e.currentTarget.value)}/>
                            </Grid>
                        </Grid>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                            disabled={invalidForm || loading}
                        >
                            Sign Up
                        </Button>
                        <br/>
                        <br/>
                        <br/>
                        <Grid container justify="center">
                            <Grid item>
                                <Link to="/sign-in" style={{
                                    textDecoration: 'none',
                                    color: '#000000de'
                                }}>
                                    Already have an account? Sign in
                                </Link>
                            </Grid>
                        </Grid>
                        <Box mt={5}>
                            <Footer/>
                        </Box>
                    </form>
                </div>
            </Container>
            {
                openDialog ? <CheckEmailVerificationDialog onClose={() => {
                    history.push(SIGN_IN);
                    setOpenDialog(false);
                }}/> : <></>

            }
        </div>
    );
};

export default SignUp;
